.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.card {
  font-weight: 400;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-color: #fff;
  background-clip: border-box;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
}

.card-body {
  margin-left: 1.5rem;
  margin-right: 1.5rem;
  margin-top: 1.5rem;
  flex: 1 1 auto;
  min-height: 1px;
  padding: 1.25rem;
}

.header {
  background-color: #000000;
  background-image: linear-gradient(315deg, #000000 0%, #958e69 74%);
  padding-top: 1rem;
}

.header-img {
  background-image: url(/static/media/header.1adda06d.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 2.5em;
}

.footer {
  background-color: #000000;
  background-image: linear-gradient(315deg, #000000 0%, #958e69 74%);
  padding-top: 1rem;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
  justify-content: center;
}

.col {
  flex: 0 0 100%;
  max-width: 100%;
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
}

.h3-text {
  font-size: 1.75rem;
  color: white;
  padding-top: 1rem;
  margin-bottom: 1rem;
  font-weight: bold;
}

.grid-container {
  max-width: 1200px;
  margin: 30px auto;
  padding: 0 20px;
  width: 100%;
  display: grid;
  /* Define Auto Row size */
  grid-auto-rows: 400px;
  /*Define our columns */
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 1em;
}

.grid-item {
  padding: 20px;
  border-radius: 10px;
  width: 300px;
  height: 400px;
}

.img-cover {
  height: 90%;
  width: 100%;
}

.navbar {
  margin-bottom: 0px;
}

.carousel-caption {
  top: 30%;
  bottom: auto;
}

.h1-text {
  font-size: 60px;
  font-weight: bold;
  color: #7ed6df;
}

.h5-text {
  font-size: 30px;
